import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-document',
  templateUrl: './document.component.html',
  styleUrls: ['./document.component.scss']
})
export class DocumentComponent implements OnInit, OnDestroy{
  @ViewChild('navbarToggler') navbarToggler: ElementRef;
  subscription: Subscription;
  isCollapsed = true;
  showDefault = false;
  docDev = false;
  constructor(private router: Router) { }

  ngOnInit(): void {
    if(window.innerWidth > 767) {
      this.docDev = true;
    }
    if (this.router.url === '/docs'){
      this.showDefault = true;
    }
    this.subscription = this.router.events.subscribe((ev) => {
      if (ev instanceof NavigationEnd) {
        if (ev.url === '/docs'){
          this.showDefault = true;
          if (this.isCollapsed === false) {
            this.isCollapsed = true;
           }
        }else{
          this.showDefault = false;
        }
      }
    });
  }

  clickOutside(event): void{
    if(event.srcElement.className.split(' ').shift() !== 'nav-link' && !event.srcElement.className.split(' ').includes('bi-chevron-right') && event.srcElement.className.split(' ').shift() !== 'default-document-page'
        && event.srcElement.className !== '' && event.srcElement.className !=="navbar-toggler-icon" && event.srcElement.className.split(' ').shift() !=="navbar" ){     
        this.router.navigateByUrl('/docs')
    }
  }

  ngOnDestroy(): void{
    this.subscription.unsubscribe();
  }

  redirectToSignUP(): void{
    this.router.navigate(['/sign-up']);
  }

  scroll() {
    const target = document.getElementById("doc-div");
    target?.scrollIntoView({ behavior: 'smooth' });
  }

}
