import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AudioCallsComponent } from './audio-calls/audio-calls.component';

import { DocumentComponent } from './document.component';
import { IosComponent } from './ios/ios.component';
import { JavaComponent } from './java/java.component';
import { KotlinComponent } from './kotlin/kotlin.component';
import { WebComponent } from './web/web.component';

const routes: Routes = [
  {
    path: '', component: DocumentComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        data: {
          title: 'J U P I T A | Get Started With Easy To Follow SDK Documentation',
          metaTitle: 'All The Tools You Need',
          metaDescription: 'Get started with easy to follow API reference material + SDKs for all platforms.'
        }
      },
      {
        path: 'android/java',
        component: JavaComponent, data: {
          title: 'J U P I T A | Java SDK Documentation',
          metaTitle: 'Java SDK Documentation',
          metaDescription: 'This SDK is developed for Android using Java and utilises Google’s Volley library to create the API calls required. This library will allow you to make the required dump API calls with Jupita.'
        }
      },
      {
        path: 'audio-calls',
        component: AudioCallsComponent, data: {
          title: 'J U P I T A | Audio Calls Documentation',
          metaTitle: 'Audio Calls Documentation',
          metaDescription: 'You can enable real-time audio analytics with Jupita using the Jupita Node.js application in combination with Twilio Media Streams & Google Speech-to-Text.'
        }
      },
      {
        path: 'android/kotlin',
        component: KotlinComponent, data: {
          title: 'J U P I T A | Kotlin SDK Documentation',
          metaTitle: 'Kotlin SDK Documentation',
          metaDescription: 'This SDK is developed for Android using Kotlin and utilises Google’s Volley library to create the API calls required. This library will allow you to make the required dump API calls with Jupita.'
        }
      },
      {
        path: 'web',
        component: WebComponent, data: {
          title: 'J U P I T A | Javascript SDK Documentation',
          metaTitle: 'Javascript SDK Documentation',
          metaDescription: 'This library will allow you to make the required dump API calls with Jupita. All API calls are made asynchronously, thus there are event listeners available to handle the API results.'
        }
      },
      {
        path: 'ios',
        component: IosComponent, data: {
          title: 'J U P I T A | iOS SDK Documentation',
          metaTitle: 'iOS SDK Documentation',
          metaDescription: 'This SDK is developed for iOS using Swift 5.4, and utilises URLSession in order to create the required API call requests.'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DocumentRoutingModule { }
